"compiled"

import EventEmitter from 'events'

if(!global.Buffer && global.ArrayBuffer)
	global.Buffer = global.ArrayBuffer
if(!global.LED1)
	global.LED1 = { write(value) { console.log('LED1', value ? '+' : '-') } }

function Stream() {
	EventEmitter.apply(this, arguments)
}

Stream.prototype = Object.create(EventEmitter)

function Readable() {
	Stream.apply(this, arguments)
}

Readable.prototype = Object.create(Stream)

console.log(Readable instanceof EventEmitter)

const stream = new Readable()

stream.on('data', data => {
	LED1.write(true)
	setTimeout(() => LED1.write(false), 200)
})

setInterval(() => {
	stream.emit('data')
}, 1000)


